.Button {
  margin-top: 20px;
  margin-left: 40%;
  margin-right: 40%;
  width: 20%;
  text-align: center;
  padding: 8px 16px;
  border-radius: 2px;
  background-color: #856c8b;
  transition: background-color 300ms linear;
  text-align: center;
  display: inline-block;
  color: #fff;
  border: 0;
  text-decoration: none;
  cursor: pointer;
  font-family: inherit;
  font-size: 18px;
  line-height: 24px;
  font-style: normal;
  font-weight: 500;
  min-width: 180px;
  box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
    0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}

.Button:hover,
.Button:focus {
  background-color: #5e63b6;
}
